import { Component, OnInit } from '@angular/core';

@Component({
  selector: 'app-vacation',
  templateUrl: './vacation.component.html',
  styleUrls: ['./vacation.component.scss']
})
export class VacationComponent implements OnInit {
  header = {
    "title" : "假期",
    "leftIcon" : {
      "icon" : "question.svg"
    },
    "rightIcon" : {
      "icon" : "exit.svg",
      "href" : "/"
    }
   }

    navs = [{
        "url" : "/vacation",
        "image": "holiday.svg",
        "name" : "假期",
        "current" : true
    },{
        "url" : "/attence",
        "name" : "考勤",
        "image": "check.svg"
    },{
        "url" : "",
        "image": "notice.svg",
        "name" : "公告"
    }]

  isSubFooter = true;


  constructor() { }

  ngOnInit() {
  }

}



