import { Component, OnInit } from '@angular/core';

@Component({
  selector: 'app-attence',
  templateUrl: './attence.component.html',
  styleUrls: ['./attence.component.scss']
})
export class AttenceComponent implements OnInit {
  // header = {"title" : "考勤"}

  navs = [{
      "url" : "/vacation",
      "image": "holiday.svg",
      "name" : "假期"
  },{
      "url" : "/attence",
      "name" : "考勤",
      "image": "check.svg",
      "current" : true
  },{
      "url" : "",
      "image": "notice.svg",
      "name" : "公告"
  }]


  header = {
    "title" : "考勤",
    "leftIcon" : {
      "icon" : "map.svg"
    },
    "rightIcon" : {
      "icon" : "exit.svg",
      "href" : "/"
    }
   }


  isSubFooter = true;

  user = JSON.parse(window.localStorage.getItem('user'));


  constructor() { }

  ngOnInit() {
  }

}



