import { BoxComponent } from './box/box.component';
import { HeaderComponent } from './header/header.component';
import { HomeComponent } from './home/home.component';
import { FooterComponent } from './footer/footer.component';
import { ApproveComponent } from './approve/approve.component';
import { BoxiconsComponent } from './boxicons/boxicons.component';
import { ApprovePendingComponent } from './approve-pending/approve-pending.component';
import { ApproveFinishedComponent } from './approve-finished/approve-finished.component';
import { SubtabsComponent } from './subtabs/subtabs.component';
