import { Component, OnInit } from '@angular/core';

import { Day } from './day';

@Component({
  selector: 'app-calendar',
  templateUrl: './calendar.component.html',
  styleUrls: ['./calendar.component.scss']
})


export class CalendarComponent implements OnInit {

  private weeks : any[][] = [];
  
  now = new Date();

  year = this.now.getFullYear();

  month = this.now.getMonth();

  private monthName:string;

  dayslength = new Date(this.year, this.month+1 , 0).getDate();

  constructor() { 

  }

  ngOnInit() {
   this.initCalendar();
  }

  turnMonth(dir:number){

     if(this.month + dir < 0){
       this.year--;
       this.month = 11;
     }else if(this.month + dir >= 12 ){
        this.year++;
        this.month = 0; 
     }else{
       this.month += dir;
     }


    this.initCalendar();
  }


  initCalendar(){
    this.weeks = [];

    const monthNames = ["January", "February", "March", "April", "May", "June",
    "July", "August", "September", "October", "November", "December"
    ];

    this.monthName = monthNames[this.month];
    this.dayslength = new Date(this.year, this.month+1 , 0).getDate();

    // Sunday - Saturday : 0 - 6


    const firstweekday = new Date(this.year , this.month , 1).getDay();
    const lastweekday = new Date(this.year , this.month+1 , 0).getDay();


    let deltaBefore = firstweekday - 1;
    let deltaAfter = 7 - lastweekday;

    deltaBefore = deltaBefore > 0 ? deltaBefore : 6 ;
    deltaAfter = deltaAfter < 7 ? deltaAfter : 0;

    for(let n=0; n < this.dayslength + deltaBefore + deltaAfter; n++){
       let weekIndex = Math.floor( n/7 );

       const d = new Date(this.year,this.month,n+1-deltaBefore ).getDate();

       const active = n >= deltaBefore && n < deltaBefore +  this.dayslength;

       let current = false;
       const newDate = new Date();

       if(newDate.getFullYear() === this.year && newDate.getMonth() === this.month && newDate.getDate() === d && active){
         current = true;
       }

       let day : Day ={
           active : active,
           name : d,
           current : current
       };

       this.weeks[weekIndex] = this.weeks[weekIndex] || [];
       this.weeks[weekIndex].push(day);
    }

  }


}



