import { Component, OnInit } from '@angular/core';

import {animate, state, style, transition, trigger} from '@angular/animations';

@Component({
  selector: 'app-vacation-apply',
  templateUrl: './vacation-apply.component.html',
  styleUrls: ['./vacation-apply.component.scss'],
   animations: [
    trigger('flyInOut', [
      state('in', style({transform: 'translateX(0px)'})),
      state('off', style({transform: 'translateX(100%)'})),
      transition("off => in", animate("500ms ease")),
      transition("in => off", animate("200ms ease"))
    ])
  ]
})
export class VacationApplyComponent implements OnInit {
  private vacationTypeListPosition:string = 'off';
  private selectedVacationType:string;

  header = {
    "title" : "申请假期",
    "leftBack" : true
  }

  types = [{
      "name" : "年休假"
  },{
      "name" : "事假"
  },{
      "name" : "探亲假"
  },{
      "name" : "婚嫁"
  },{
    "name" : "串休"
  },{
    "name" : "补打卡KA"
  },{
    "name" : "外勤"
  },{
    "name" : "出差"
  }]


  navs = [{
      "url" : "/vacation",
      "image": "holiday.svg",
      "name" : "假期",
      "current" : true
  },{
      "url" : "/attence",
      "name" : "考勤",
      "image": "check.svg"
  },{
      "url" : "",
      "image": "notice.svg",
      "name" : "公告"
  }]

  isSubFooter = true;


  constructor() { }

  ngOnInit() {
  }


  selectVacationType(type:string){
    this.selectedVacationType = type;
    this.showVacationTypeList('off');
  }

  showVacationTypeList(status:string){
    this.vacationTypeListPosition = status;
  }

}



