import {Component, OnInit} from '@angular/core';

import {ActivatedRoute} from '@angular/router';

import {Location} from '@angular/common';


@Component({
    selector: 'app-box',
    templateUrl: './box.component.html',
    styleUrls: ['./box.component.scss']
})
export class BoxComponent implements OnInit {
    header = {
        "title": "百宝盒"
    }

    navs = [{
        "url": "/",
        "image": "home.svg",
        "name": "首页",
    }, {
        "url": "",
        "image": "info.svg",
        "name": "消息"
    }, {
        "url": "/box",
        "image": "box.svg",
        "name": "百宝箱",
        "current": true
    }, {
        "url": "",
        "image": "man.svg",
        "name": "我的"
    }]

    box = {
        headline: "所有功能",
        subTitle: "可在此定制化展示在首页的功能",
        boxicons: [{
            "url": "",
            "image": "air.svg",
            "name": "差旅管理"
        }, {
            "url": "",
            "image": "money_card.svg",
            "name": "薪资查询"
        }, {
            "url": "",
            "image": "examine.svg",
            "name": "统一审批"
        }, {
            "url": "",
            "image": "attendance.svg",
            "name": "考勤假期"
        }, {
            "url": "",
            "image": "meeting.svg",
            "name": "会议管理"
        }, {
            "url": "",
            "image": "message.svg",
            "name": "及时通信"
        }, {
            "url": "",
            "image": "questionnaire.svg",
            "name": "问卷调查"
        }]
    }


    constructor(private location: Location) {
    }

    ngOnInit() {
    }

    goBack(): void {
        this.location.back();
    }


}



