import { Injectable } from '@angular/core';
import { HttpClient, HttpHeaders } from '@angular/common/http';

import { Observable } from 'rxjs/Observable';

import { of } from 'rxjs/observable/of';
import { catchError, map, tap,timeout } from 'rxjs/operators';

import { MessageService } from './message.service';

import { User } from './user';

const httpOptions = {
  headers: new HttpHeaders({ 'Content-Type': 'application/json' })
};

@Injectable()
export class LoginService {

  private domain = 'http://10.226.62.112:9080';

  private loginUrl = 'http://10.226.62.112:9080/mfp/api/adapters/OA/authorization/login';
  private localLoginUrl = 'http://192.168.3.113:8080/mfp/api/adapters/OA/authorization/login';

  constructor(
    private http: HttpClient ,
    private messageService: MessageService
    ) {
  }

  login (uid:string,password:string): Observable<User>{
    const url = `${this.localLoginUrl}?uid=${uid}&password=${password}`;
    alert(url);
    return this.http.get<User>(url)
      .pipe(
        tap( _ => {} ),
        catchError(this.handleError('login', {  "success" : false, 
                                                "error" : {
                                                  message : "network error",
                                                  name : "",
                                                  code : "",
                                                  paths : ""
                                                  },"data" : {} })
                )
      );
  }

  private log(message: string) {

  }

  private handleError<T> (operation = 'operation', result?: T) {
    return (error: any): Observable<T> => {

      // TODO: send the error to remote logging infrastructure
      console.error(error); // log to console instead

      // TODO: better job of transforming error for user consumption
      this.log(`${operation} failed: ${error.message}`);

      // Let the app keep running by returning an empty result.
      return of(result as T);
    };
  }
}



